
export default {
  name: "app",
  data() {
    return {
      audioElement: new Audio(
        "https://ia600900.us.archive.org/32/items/tvtunes_27340/Rick%20and%20Morty.mp3"
      ),
      audioOn: true,
    };
  },
  mounted() {
    this.audioElement.play();
  },
  methods: {
    audioPause() {
      this.audioElement.pause();
      this.audio = false;
    },
    audioRun() {
      this.audioElement.play();
      this.audio = true;
    },
  },
};
